'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { useEffect, useState } from 'react';

interface Category {
  _id: string;
  name: string;
  gender: string;
  dresses: string[];
  createdAt: string;
  updatedAt: string;
  __v: number;
}

const Header: React.FC = () => {
  const pathname = usePathname();
  const [categories, setCategories] = useState<Category[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const response = await fetch('http://localhost:5000/api/clothing/');
        const data = await response.json();
        setCategories(data);
        setLoading(false);
      } catch (error) {
        console.error('Error fetching categories:', error);
        setLoading(false);
      }
    };

    fetchCategories();
  }, []);

  const getCategoryLink = (categoryName: string, gender: string) => {
    const formattedName = categoryName.toLowerCase().replace(/ & /g, '-').replace(/ /g, '-');
    const basePath = gender === 'women' ? '/collections/womens' : '/collections/mens';
    return `${basePath}/${formattedName}`;
  };

  const currentGender = pathname?.startsWith('/womens') ? 'women' : 'men';

  return (
    <header className="border-b border-pink-200 bg-pink-50">
      {/* Top Navigation */}
      <div className="flex justify-between items-center px-6 py-2 text-sm font-medium uppercase text-pink-800">
        <div className="flex space-x-6">
          <Link href="/" className="hover:text-pink-600 transition-colors">Men</Link>
          <Link href="/womens" className="hover:text-pink-600 transition-colors">Women</Link>
        </div>
        <div className="text-lg font-semibold">
          <Link href="/" className="text-pink-900">Style Nest</Link>
        </div>
        <div className="flex space-x-6">
          <Link href="/search" className="hover:text-pink-600 transition-colors">Search</Link>
          <Link href="/account" className="hover:text-pink-600 transition-colors">Account</Link>
          <Link href="/cart" className="hover:text-pink-600 transition-colors">Bag</Link>
        </div>
      </div>

      {/* Bottom Navigation */}
      <div className="flex justify-center space-x-6 px-6 py-2 text-sm font-medium uppercase border-t border-pink-200 bg-pink-100 text-pink-700">
        {loading ? (
          <span>Loading...</span>
        ) : (
          categories
            .filter((category) => category.gender === currentGender)
            .map((category) => (
              <Link
                key={category._id}
                href={getCategoryLink(category.name, category.gender)}
                className="hover:text-pink-500 transition-colors"
              >
                {category.name}
              </Link>
            ))
        )}
      </div>
    </header>
  );
};

export default Header;